import React from 'react';
import './PersonCard.css';
import { ArrowsAngleExpand, ChevronRight, CheckSquare} from 'react-bootstrap-icons';

class PersonCard extends React.Component {
    constructor() {
        super();
        this.state = {
            bodyStyle: 'displayNone'
        };
    }

    toggleBody = () => {
        if (this.state.bodyStyle === 'displayNone') {
           this.setState({bodyStyle: 'peopleCardBody'})
        } else if (this.state.bodyStyle === 'peopleCardBody') {
          this.setState({bodyStyle: 'displayNone'}) 
        }
    }

    render() {
        const taskCount = 0;
        return ( 
           //card
           <div className='peopleCard'>

                {/* card head */}
                <button className='peopleCardHeader' onClick={this.toggleBody} >
                    <div className='flexCenter flexRow'>
                        <p>Thomas</p>
                        <span className='taskCount'>{taskCount}</span>
                    </div>
                    <ArrowsAngleExpand size={20} />
                </button>

                {/* Card Body */}
                <div className={this.state.bodyStyle}> 
                    <div>
                        <p>Math</p>
                        <p>Lesson 1</p> 
                    </div>

                    <div>
                        <button className='iconBtn' onClick={ () => alert('marked complete') }>
                            <CheckSquare className='icon' size={25} />
                        </button>
                                    
                        <button className='iconBtn' onClick={ () => alert('show form') } >
                            <ChevronRight className='icon' size={25} />
                        </button>
                    </div>
                </div> 
            </div>
            
        )
    }
}

export default PersonCard;