import React from 'react';
import './HomeTab.css';
import PersonCard from '../PersonCard/PersonCard';

class HomeTab 
extends React.Component {
    constructor() {
        super();
        this.state = {
            route: '',
        };
    }

    render() {
        return (
          <div> 
            <PersonCard />
            <PersonCard />
            <PersonCard />
          </div>
        
        )
    }
}

export default HomeTab;

